.masthead {
  width: 100vw;
  padding: 1rem 2rem;
  background-color: #111;
  color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  top: 0;
  left: -2rem;
  box-shadow: 0 2px 4px #444;
}

.masthead h1 {
  margin-left: 4rem;
  flex-basis: 38vw;
  flex-grow: 0;
  flex-shrink: 1;
}
